/*  BUTTON styles from here */

.center-buttons-middle {
	position: relative;
	display: flex;
	height: 80%;
	align-items: center;
	justify-content: center;
}

a.btn {
	text-decoration: none;
	color: #fff;
	position: relative;
	background: rgb(226, 3, 3);
	border-radius: 15px;
	padding: 30px 50px;
	margin: 0;
	letter-spacing: 1px;
	overflow: hidden;
	transition: 0.3s all ease-in-out;
	box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14),
		0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.3);
}

#button-text-color {
	color: white !important;
	text-decoration: none;
}

a.btn .wave {
	opacity: 1;
	position: absolute;
	top: 0;
	left: 50%;
	background: rgb(236, 45, 45);
	width: 520px;
	height: 532px;
	margin-left: -250px;
	margin-top: -475px;
	-webkit-transform-origin: 50% 49%;
	        transform-origin: 50% 49%;
	border-radius: 43%;
	box-shadow: 0 16px 24px 2px rgba(58, 53, 53, 0.14),
		0 6px 30px 5px rgba(58, 53, 53, 0.14),
		0 8px 10px -5px rgba(58, 53, 53, 0.14);
	-webkit-animation: drift 8000ms infinite linear;
	        animation: drift 8000ms infinite linear;
}

a.btn.rumble {
	-webkit-animation: rumbleinthejungle 300ms linear;
	        animation: rumbleinthejungle 300ms linear;
}

a.btn .text {
	position: relative;
	z-index: 2;
}

a.btn:hover {
	box-shadow: 0 0px 10px 2px rgba(0, 0, 0, 0.1),
		0 6px 10px 5px rgba(0, 0, 0, 0.12), 0 8px 5px -5px rgba(0, 0, 0, 0.3);
}

.alert {
	position: absolute;
	top: -50px;
	letter-spacing: 1px;
	color: #455a64;
	transition: all 0.3s ease-in-out;
	opacity: 0;
	left: 0;
	right: 0;
	width: 100%;
	text-align: center;
}

.alert.show {
	opacity: 1;
	top: 40px;
}

@-webkit-keyframes drift {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	from {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes drift {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	from {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@-webkit-keyframes rumbleinthejungle {
	0% {
		-webkit-transform: translatex(0);
		        transform: translatex(0);
	}
	25% {
		-webkit-transform: translatex(-5px);
		        transform: translatex(-5px);
	}
	50% {
		-webkit-transform: translatex(0);
		        transform: translatex(0);
	}
	75% {
		-webkit-transform: translatex(5px);
		        transform: translatex(5px);
	}
	100% {
		-webkit-transform: translatex(0);
		        transform: translatex(0);
	}
}

@keyframes rumbleinthejungle {
	0% {
		-webkit-transform: translatex(0);
		        transform: translatex(0);
	}
	25% {
		-webkit-transform: translatex(-5px);
		        transform: translatex(-5px);
	}
	50% {
		-webkit-transform: translatex(0);
		        transform: translatex(0);
	}
	75% {
		-webkit-transform: translatex(5px);
		        transform: translatex(5px);
	}
	100% {
		-webkit-transform: translatex(0);
		        transform: translatex(0);
	}
}

